@import "https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;700&display=swap";
*, h1 {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  font-family: Open Sans;
  overflow-x: hidden;
}

main {
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
  display: flex;
}

.nav {
  background-color: #f6f6f6;
  padding: 0 15px;
}

.nav_content {
  justify-content: space-between;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  padding: 10px 0;
  display: flex;
}

.nav a {
  color: #000;
  text-align: center;
  border-radius: 5px;
  align-items: center;
  gap: 10px;
  padding: 5px 10px;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}

@media (width >= 768px) {
  .nav a {
    font-size: 24px;
  }
}

.nav a:hover {
  color: #000;
  background-color: #0000;
}

section {
  width: 100%;
  max-width: 1000px;
  margin: 40px auto;
  padding: 0 15px;
}

@media (width >= 1000px) {
  section {
    padding: 0;
  }
}

section h1 {
  margin: 10px 0;
}

@media (width >= 768px) {
  section h1 {
    padding: 0;
    font-size: 42px;
  }
}

.section_heading {
  margin-bottom: 40px;
}

.converter_field {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.field_element {
  flex-direction: column;
  display: flex;
}

input {
  appearance: none;
  background-color: #fff;
  border: 1px solid #bbb;
  border-radius: 5px;
  outline-color: #ffde34;
  padding: 15px 10px;
}

select {
  appearance: none;
  color: #000;
  cursor: pointer;
  background: #fff url("https://ik.imagekit.io/zxaewhloxf/arrow.svg?updatedAt=1703427789279") 98% no-repeat;
  border: 1px solid #bbb;
  border-radius: 5px;
  outline-color: #ffde34;
  padding: 15px 10px;
}

label {
  z-index: 1;
  background-color: #fff;
  width: -moz-fit-content;
  width: fit-content;
  margin: -9px 10px;
  padding: 0 10px;
  font-weight: 500;
}

.secondaryButton {
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  display: flex;
}

.secondaryButton:hover {
  color: #000;
  background-color: #0000;
}

.dropdown {
  z-index: 2;
  position: relative;
}

.dropdown_content {
  text-align: center;
  visibility: hidden;
  background-color: #fff;
  border-radius: 5px;
  min-width: 250px;
  padding: 0 20px;
  list-style-type: none;
  transition: all .1s cubic-bezier(.16, 1, .5, 1);
  position: absolute;
  right: 0;
  transform: translateY(-20px);
  box-shadow: 0 8px 16px #0003;
}

.show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.dropdown_content li {
  border-bottom: 2px solid #e7e7e7;
  padding: 20px 0;
}

.dropdown_content li:last-of-type {
  border-bottom: none;
}

@media (hover: hover) {
  .secondaryButton:hover, .nav a:hover {
    background-color: #ddd;
  }
}

.convertButton {
  color: #fff;
  cursor: pointer;
  background-color: #09f;
  border: none;
  border-radius: 5px;
  margin: 10px 0;
  padding: 15px 10px;
}

.convertButton:active {
  background-color: #50b6fb;
}

.output p {
  margin: 10px 0;
  font-size: 30px;
  font-weight: bold;
}

@media (width >= 768px) {
  .output p {
    font-size: 60px;
  }
}

footer {
  color: #fff;
  background-color: #333;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  display: flex;
}

footer p {
  text-align: center;
  width: 100%;
  padding: 10px;
}

